@import url(https://fonts.googleapis.com/css2?family=Sawarabi+Mincho&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:url(/favouritePlanetTask/static/media/krDPXb.8fe990ca.jpg);background-size:cover;background-repeat:no-repeat;color:white !important}body .App{font-family:"Sawarabi Mincho", sans-serif}body .column{padding:1.5rem}body .column .divider::before{content:"";width:100px;height:4px;position:absolute;top:15%;left:50%;background-color:white;transform:translateX(-50%);border-radius:10px}body .column::before{content:"";width:80%;height:100%;position:absolute;top:1%;left:10%;background-color:rgba(0,0,0,0.45);border-radius:10px;z-index:-1}body .column ul{list-style-type:none;width:60%}body .column ul li{margin-bottom:20px;font-size:18px;display:flex;justify-content:space-around;align-items:center}body .column ul li i{cursor:pointer;position:absolute;right:30%}body .column ul li .filled{color:red;font-size:30px;transition:0.2s}body .column ul li .none{color:white;font-size:22px;transition:0.22s}.status{position:absolute;bottom:3%;left:50%;transform:translate(-50%, -50%);border:1px solid red;background:rgba(255,255,255,0.5);color:black;font-weight:900;font-size:17px}a{text-decoration:none;color:white !important}a:hover{text-decoration:none !important;color:red !important}.blue-color{color:#008cff}@media screen and (max-width: 1000px){body,h1,p{font-size:7vw !important}.divider::before{display:none}}

